@import url(https://fonts.googleapis.com/css?family=Poppins:400,700);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

*::-moz-selection {
  color: #ffeeb7;
  background: black;
}

*::selection {
  color: #ffeeb7;
  background: black;
}

html, body {
  min-height: 100vh;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Poppins', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.app {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #ffeeb7;
  min-height: 100vh;
}

.container {
  align-self: flex-start;
  transition: flex 0.3s ease-in-out;
  flex: 0 0;
  overflow: hidden;
}

.move-up {
  flex: 1 1;
}

.app > h1 {
  text-align: center;
  display: block;
  max-width: 100vw;
}

.user-search {
  width: 80vw;
  max-width: 300px;
}

.user-search > input[type=text] {
  max-width: inherit;
  width: inherit;
  display: block;
  font-family: inherit;
  font-size: 1em;
  border: none;
  margin-top: 2em;
  padding: 0.5em 0 0.5em 0.3em;
  background-color: transparent;
}

.user-search::after {
  display: block;
  content: '';
  border-bottom: solid 0.3em black;
  transition: -webkit-transform 0.2s ease-in-out;
  transition: transform 0.2s ease-in-out;
  transition: transform 0.2s ease-in-out, -webkit-transform 0.2s ease-in-out;
  -webkit-transform: scaleX(0.4);
          transform: scaleX(0.4);
  -webkit-transform-origin: 0 50%;
          transform-origin: 0 50%;
}

.user-search:focus-within::after {
  -webkit-transform: scaleX(1);
          transform: scaleX(1);
}

.now-playing {
	display: flex;
	flex-direction: column;
	flex-wrap: wrap;
	padding: 1em;
}

.now-playing > h2 {
	margin: 0.6em;
}

@-webkit-keyframes slide-up {
	0% {
		-webkit-transform: translateY(100%);
		        transform: translateY(100%)
	}

	100% {
		-webkit-transform: translateY(0);
		        transform: translateY(0);
	}
}

@keyframes slide-up {
	0% {
		-webkit-transform: translateY(100%);
		        transform: translateY(100%)
	}

	100% {
		-webkit-transform: translateY(0);
		        transform: translateY(0);
	}
}

.album-art {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	overflow: hidden;
	-webkit-animation: 0.3s ease-in-out 1 slide-up;
	        animation: 0.3s ease-in-out 1 slide-up;
}

.album-art > * {
	margin: 0.6em;
}

.album-art > .album-img {
	width: 300px;
	padding-bottom: 300px;
	position: relative;
	height: 0;
	overflow: hidden;
}

.album-art > .album-img > img {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
}

.info {
	max-width: 500px;
}

.info > span {
	display: table;
	color: #ffeeb7;
	background-color: black;
	padding: 0.4em;
	text-transform: lowercase;
	margin-bottom: 0.6em;
}

.lyrics {
	background-color: black;
	color: #ffeeb7;
	padding: 1em;
}

.lyrics > span {
	white-space: pre-line;
}
